.dota-roll .pic-contain {
  position: absolute;
  overflow: hidden;
  z-index: -1;
  text-align: center;
}
.dota-roll .pic-contain .bg {
  display: inline-block;
  opacity: .3;
  -webkit-transform-origin: 50% 50%;
          transform-origin: 50% 50%;
  -webkit-transition: opacity 3s, -webkit-transform 15s;
  transition: opacity 3s, -webkit-transform 15s;
  transition: transform 15s, opacity 3s;
  transition: transform 15s, opacity 3s, -webkit-transform 15s;
  height: 100%;
  -webkit-transform: scale(1.08);
          transform: scale(1.08);
}
.dota-roll .pic-contain .bg-transition {
  opacity: 1;
  -webkit-transform: scale(1);
          transform: scale(1);
}
.dota-roll .pic-contain .bg-shadow {
  position: absolute;
  top: 0;
  background-color: rgba(0, 0, 0, 0.4);
}
.dota-roll .roll-pannel {
  position: absolute;
  left: 50%;
  top: 50%;
  padding: 30px 40px 0px 40px;
  width: 300px;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  background-color: rgba(50, 50, 50, 0.6);
}
.dota-roll .roll-pannel .roll {
  margin-bottom: 30px;
  width: 300px;
  height: 40px;
  overflow: hidden;
  font-weight: 500;
  line-height: 40px;
  border-radius: 2px;
  font-size: 13px;
  cursor: pointer;
  white-space: nowrap;
  background-color: rgba(70, 175, 95, 0.9);
  color: #fff;
  -webkit-transition: all 0.25s cubic-bezier(0.165, 0.84, 0.44, 1), opacity 1ms linear;
  transition: all 0.25s cubic-bezier(0.165, 0.84, 0.44, 1), opacity 1ms linear;
}
.dota-roll .roll-pannel .roll:hover {
  background-color: rgba(90, 225, 125, 0.9);
}
.dota-roll .roll-pannel .roll-item {
  border-bottom: 1px solid rgba(50, 50, 50, 0);
  padding: 10px 0 10px 60px;
  width: 240px;
  font-size: 15px;
  color: #ccc;
  text-align: left;
  -webkit-transition: all 0.25s cubic-bezier(0.165, 0.84, 0.44, 1), opacity 1ms linear;
  transition: all 0.25s cubic-bezier(0.165, 0.84, 0.44, 1), opacity 1ms linear;
}
.dota-roll .roll-pannel .roll-item:hover {
  cursor: pointer;
  color: white;
  background-color: rgba(50, 50, 50, 0.6);
}
.dota-roll .roll-pannel .roll-item .desc {
  display: inline-block;
  width: 50%;
}
